<template>
    <div class="banner-widget">
        <div class="container">
            <img class="b-w-bg" :src="banner_src.image" />
            <p class="b-w-title">{{banner_src.name}}</p>
            <div class="b-w-desc basic-article-widget">
                <p class="ba-w-p" v-for="desc of banner_desc">{{desc}}<p/>
            </div>
            <p>
                <router-link :to="'/learn_start?class_id=' + (curClass && curClass.id)" class="b-w-btn">开启我的征途</router-link>
            </p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "banner-widget",
        props: ["cur-class"],
        data(){
            return {

                /*banner资源*/
                banner_src: "",

            }
        },
        computed: {
            banner_desc(){
                let bs = this.banner_src;
                if(bs && bs.content){
                    return bs.content.split("\\n");
                }else{
                    return []
                }
            }
        },
        mounted(){
            this.$utils.get_res("banner_src", this);
        }
    }
</script>

<style scoped>
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/banner-widget.css";
</style>


